@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.dropDown{position:absolute;width:465px;height:300px;top:30px;right:-15px;background-color:#fff;box-shadow:0 0 10px 1px rgba(48,50,50,0.3);overflow:auto}.dropDown input{width:100%;padding:5px 16px;font-size:14px;color:#575757;outline:none;border:none;border-bottom:1px solid #cdd7e7}.dropDown input::-webkit-input-placeholder{color:#cdd7e7}.dropDown input:-ms-input-placeholder{color:#cdd7e7}.dropDown input::placeholder{color:#cdd7e7}.dropDown .dropTable_header{font-size:12px;line-height:14px;color:#575757;background-color:#f2f5f8;padding:9px 16px 4px}.dropDown .dropTable_data{display:flex;justify-content:space-between;height:30px;padding:9px 16px 4px;cursor:pointer}.dropDown .dropTable_data:hover{background-color:#f2f5f8}.dropDown .dropTable_data-row{width:400px}.dropDown .dropTable_data span{font-family:Roboto, sans-serif;font-size:12px;line-height:14px;color:#575757}.dropDown .dropTable_data span{vertical-align:super}.dropDown .dropTable_data span+span{margin-left:10px}.dropDown .dropTable_data .star{position:relative;width:25px;height:25px}.dropDown .dropTable_data .star svg{position:absolute;top:-6px;right:-3px}.dropDown .dropTable_data .star svg:hover{transform:scale(1.3)}

.box{position:relative;width:465px;height:110px}.box label{display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start;height:110px;padding:8px 16px 4px;border:1px solid #cdd7e7}.box label:focus-within{border-color:#99aece}.box label span{font-family:Roboto, sans-serif;font-size:12px;line-height:14px;color:#575757}.box label input::-webkit-outer-spin-button,.box label input::-webkit-inner-spin-button{-webkit-appearance:none}.box label input[type='number']{-moz-appearance:textfield}.box label input{padding-right:60px;font-size:28px;line-height:33px;font-weight:bold;color:#2d2d2d;border:none;outline:none;width:100%}.box .button{position:absolute;top:calc(50% - 10px);right:15px}.box .button button{outline:none;border:none;background-color:#fff}.box .button button:hover{cursor:pointer}.box .button button:hover span{color:#99aece}.box .button button img{display:inline-block;vertical-align:middle;margin-bottom:5px}.box .button button .down{transform:rotate(180deg)}

.all-currency{margin:50px 0;padding:10px 20px 30px;border-radius:5px;background-color:#fff;box-shadow:0 0 24px 10px rgba(48,50,50,0.1)}.all-currency h3{margin:15px 0 10px;font-size:12px;line-height:16px;color:#575757}.all-currency table{width:100%;border-collapse:collapse}.all-currency table th,.all-currency table td{padding:8px 16px;font-size:14px;color:#575757;border-bottom:1px solid #cdd7e7}.all-currency table th:last-child{text-align:right}.all-currency table tr:hover{cursor:pointer}.all-currency table tr:hover td{background-color:#EBEBEA}.all-currency table tr:active td{background-color:#dadad8}.all-currency table tr td:first-child{text-align:left}.all-currency table tr td:nth-child(3){text-align:center}.all-currency table tr td:last-child{text-align:right;font-weight:bold}.all-currency button{position:relative;width:150px;margin:25px 0 10px;padding:5px 15px;left:50%;transform:translateX(-50%);border:1px solid #B1AFA9;border-radius:5px;outline:none;cursor:pointer;background-color:#ffffff;transition:all 0.2s}.all-currency button:hover{border:1px solid #7A7666}.all-currency button:active{border:1px solid #45433B}

.App{max-width:1000px;margin:0 auto}.App .exchanger{margin-top:50px;padding:10px 20px 30px;border-radius:5px;background-color:#fff;box-shadow:0 0 24px 10px rgba(48,50,50,0.1)}.App .exchanger_header{display:flex;justify-content:space-between}.App .exchanger_header h1{margin:15px 0 10px;font-size:15px;color:#11171F}.App .exchanger_header h3{margin:15px 0 10px;font-size:12px;line-height:16px;color:#575757}.App .exchanger_header h3 span{margin-left:10px}.App .exchanger_header h3 span input{outline:none;border:none}.App .exchanger_container{position:relative;display:flex;justify-content:space-between;align-items:center}.App .exchanger_container .replacement{position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:center;width:48px;height:48px;right:calc(50% - 24px);border-radius:50%;background-color:#fff;box-shadow:0 0 10px 1px rgba(48,50,50,0.3);cursor:pointer;transition:ease-in-out .2s;z-index:2}.App .exchanger_container .replacement:active{transform:scale(1.1)}.App .exchanger_container .replacement .arrow-right,.App .exchanger_container .replacement .arrow-left{position:relative;width:15px;height:2px;background-color:#050505}.App .exchanger_container .replacement .arrow-right{margin-bottom:6px}.App .exchanger_container .replacement .arrow-right:after{content:'';position:absolute;width:0;height:0;border-style:solid;border-width:2px 0 2px 4px;border-color:transparent transparent transparent #050505;top:-1px;right:-2px}.App .exchanger_container .replacement .arrow-left:after{content:'';position:absolute;width:0;height:0;border-style:solid;border-width:2px 4px 2px 0;border-color:transparent #050505 transparent transparent;top:-1px;left:-2px}

*,
*::before,
*::after {
  box-sizing: border-box;
}


ul[class],
ol[class] {
  padding: 0;
}


body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;

}


body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: Roboto, sans-serif;
  background-color: #f2f5f8;

}


ul[class],
ol[class] {
  list-style: none;
}


a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}


img {
  max-width: 100%;
  display: block;
}


article > * + * {
  margin-top: 1em;
}


input,
button,
textarea,
select {
  font: inherit;
}


@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

